.top_ulcss{
    font-family: 'Noto Sans KR', sans-serif;
    margin: 0 auto;
    width:1100px;
    height:inherit;
}
.top_ulcss>li:nth-child(1){
    height:70px;
    background-color: azure;
}

.top_ulcss>li:nth-child(2){
        display: flex;
        justify-content: space-between;
        height:40px;

}
.top_ulcss>li:nth-child(3){

    height:110px;
    background-color: orangered;
}
.top_middle{
    list-style: none;
    
    display: inline;
    height:inherit;

  

    right:0;

}
.top_ulcss>li:nth-child(2)>span{
    width:450px;
    line-height: 40px;
}

.top_middle>li{
    display:inline-block;
    height:inherit;
    line-height: 40px;
    padding:0 20px 0 20px;
    box-sizing: border-box;
    cursor: pointer;
    position: relative;
}

.top_middle>li:nth-child(4){
    color:red;
}
.top_middle>li::after{
    content: "|";
    position:absolute;
    right:0;

}
.top_middle>li:last-child::after{
    content: "";
}
.basket_font{
    color:red;
}